export default {
  name: "order",
  props:['value'],
  data() {
    return {
      id: this.value.ID,
      name: this.value.NAME,
      img: this.value.SMALL_IMG,
      price: this.value.MARKET_PRICE,
      disPrice: 999,
      proNum: 1,
    }
  },
  methods: {
    
  }
};



